<template>
	<view class="uni-withdrewal">
		<view class="uni-money-detail">
			
				<view class="uni-title">提现金额 (元)</view>
				<view class="uni-important"><view>￥</view><input class="tetts"v-model="money"></input></view>		
			
		    <view class="uni-tips">当前可提现{{extra/100}}元</view>
		</view>
		<view class="zhifubao styles zfyhtitle" @tap.stop="zfbFlag=true,yhFlag=false,name='',accountNum='',yhAccName=''">
			<image class="img" src="data:image/png;base64,"></image>
	        <text class="word">提现到支付宝</text>	
		</view>
		<view class="zhi-list zfyhList" v-if="zfbFlag">
			<input class="styles word borderLine" v-model="name" placeholder="请输入您的姓名" />
			<input class="styles word" v-model="accountNum" placeholder="请输入您的支付宝账号" />
		</view>
		<view class="cards styles  zfyhtitle" @tap.stop="zfbFlag=false,yhFlag=true,name='',accountNum=''">
			<image class="img" src="data:image/jpg;base64,"></image>
		    <text class="word">提现到银行卡</text>
		</view>
		<view class="yh-list zfyhList" v-if="yhFlag" >
			<input class="styles word " v-model="name" placeholder="请输入您的姓名" />
			<input class="styles word borderLine" v-model="yhAccName" placeholder="请输入提现银行" />
			<input class="styles word" v-model="accountNum" placeholder="请输入银行卡号" />
		</view>
		<view class="submit" @tap.stop="submits">确认提交</view>
	</view>
</template>

<script>
	export default{
		data(){
			return {
				name:'',
				money:'',
				accountNum:'',
				yhAccName:"",
				zfbFlag:false,
				yhFlag:false,
				extra:''
			}
		},
		onLoad(){
			this.extra =wx.getStorageSync('user').money;
		},
		methods:{
			submits(){
				let type =0;
				let id =wx.getStorageSync('user').id;
				if(this.zfbFlag==true){
					type=2
				}else if(this.yhFlag==true){
					type=1
				}
				if(type ==1 ||type ==2){
					uni.wjw_http({
						url:'userwithdraw/save',
						data:{
							userId:id,
							type:type,
							cardNum:this.accountNum,
							realname:this.name,
							bankName:this.yhAccName	,
							money:this.money
						}
					}).then(res=>{
						if(res.status ==0){
						let types=6;
						uni.navigateTo({
							url:'/pages/orderMsg/successPage?type='+types
						})
							
						}
					})
				}
				
			}
		}
	}
	
</script>

